//Get api url
const base_url = "https://api.jikan.moe/v3";

class searchManga {
  constructor(){
    this.getElements();
    this.fetchMangaData();
    searchManga.displayManga();
  }

  getElements(){
  this.searchForm = document.getElementById('search-form');
  this.selectedLimit = document.getElementById('limit');
  this.selectedGenre = document.getElementById('genre');
  this.searchBtn = document.getElementById('search-btn');
  this.searchInput = document.getElementById('search-input');
  }

  //Fetch Manga Data
  fetchMangaData (){
    this.searchForm.addEventListener('submit', (e) => {
      e.preventDefault();
      //search word
      this.searchTerm = this.searchInput.value;
      // when searchTerm is null, show alert
      if(this.searchTerm == "") {
        alert('„Ç≠„Éº„ÉØ„Éº„Éâ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ')
      } else {
        fetch(`${base_url}/search/manga?q=${this.searchTerm}&page=1&genre_exclude=33,12,28,34,43&limit=${this.selectedLimit.value}`)
        .then(res=>res.json())
        .then(searchManga.displayManga)
        .catch(err=>console.warn(err.message));
      }
      })
  }

  //UI
  static displayManga(items) { //data --> res.json()„ÅÆÂÜÖÂÆπ
    console.log(items);
    let output = '<div class="row">';
    items.results.forEach(item => {
      output += `
        <div class="col-md-4" style="margin-bottom:4rem;">
        <h5 class="card-title">${item.title}</h5>
        <img src=${item.image_url}>
        <p class="card-text">${item.synopsis}.</p>
        <a class="btn btn-secondary" href="${item.url}" target="_blank">View Details</a><button class="addBtns"> +</button>
        </div>
      `
    });
    output += `</div>`
    document.getElementById('search-results').innerHTML = output;

    const addBtns = document.querySelectorAll('.addBtns')
    addBtns.forEach(addBtn => {
      addBtn.addEventListener('click', Fake.displayname)
});
  }
}

////////////‰ª•‰∏ã„ÅäÊ∞ó„Å´ÂÖ•„Çä„É™„Çπ„Éà///////////////////

class Manga {
  constructor(image, title, link) {
    this.image = image;
    this.title = title;
    this.link = link;
  }
}

class Fake {
  static displayname() {
    const books = '„Åà„Å∏„Å∏'
    console.log(books);
  }
}

class UI {
  static displayBooks() {
    const books = Storage.getBooks();
    books.forEach((book)=> UI.addBooksToList(book));
  }

}

class Storage {
  static getBooks() {
    let books;
    //  getItem() „É°„ÇΩ„ÉÉ„Éâ„ÅØ„Ç≠„Éº„ÅÆÂêçÁß∞„ÇíÊ∏°„Åô„Å®„ÄÅ„Åù„ÅÆ„Ç≠„Éº„Å´ÂØæ„Åô„ÇãÂÄ§„ÇíËøî„Åó„Åæ„Åô„ÄÇ
    if(localStorage.getItems('books') === null) {
      books = [];
    } else {
      // JSON.parse() „É°„ÇΩ„ÉÉ„Éâ„ÅØÊñáÂ≠óÂàó„Çí JSON „Å®„Åó„Å¶Ëß£Êûê„Åó„ÄÅÊñáÂ≠óÂàó„Å´„Çà„Å£„Å¶Ë®òËø∞„Åï„Çå„Å¶„ÅÑ„Çã JavaScript „ÅÆÂÄ§„ÇÑ„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„ÇíÊßãÁØâ„Åô„Çã
      books = JSON.parse(localStorage.getItem('books'))
    }
    return books;
  }

  static addBooksToList(book){
    const books = Storage.getBooks();//books„ÅåËøî„Çã
    books.push(book);
    //JavaScript „ÅÆ„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„ÇÑÂÄ§„Çí JSON ÊñáÂ≠óÂàó„Å´Â§âÊèõ„Åó„Åæ„Åô„ÄÇ
    localStorage.setItem('books', JSON.stringify(books));
  }

}

